import { useEffect, useState } from 'react';
import { useNavigate, useParams, Link } from 'react-router-dom';
import { FiArrowLeft, FiSave, FiUpload } from 'react-icons/fi';
import { createItem, updateItem, getItemById, uploadItemImage, validateItem } from '../../services/itemService';
import { useNotification } from './NotificationSystem';

export default function ItemForm() {
  const { id } = useParams();
  const isEdit = Boolean(id) && id !== 'new';
  const navigate = useNavigate();
  const { showError, showSuccess } = useNotification();
  const [loading, setLoading] = useState(false);
  const [errors, setErrors] = useState({});
  const [form, setForm] = useState({
    name: '', type: 'product', hsn: '', code: '', unit: '', category: '', imageUrl: '',
    pricing: { salePrice: '', purchasePrice: '', discountType: 'percentage', discountValue: '', taxRate: '', taxMode: 'without_tax' },
    stock: { track: false, openingQty: '', minQty: '', location: '' },
    status: 'active', notes: ''
  });

  useEffect(() => { if (isEdit) load(); }, [id]);

  const load = async () => {
    try {
      setLoading(true);
      const data = await getItemById(id);
      if (data) setForm({ ...data });
    } catch (e) { showError('Failed to load item'); } finally { setLoading(false); }
  };

  const handleChange = (e) => {
    const { name, value, type, checked } = e.target;
    setForm((prev) => ({ ...prev, [name]: type === 'checkbox' ? checked : value }));
  };

  const handlePricingChange = (e) => {
    const { name, value } = e.target;
    setForm((prev) => ({ ...prev, pricing: { ...prev.pricing, [name]: value } }));
  };

  const handleStockChange = (e) => {
    const { name, value, type, checked } = e.target;
    setForm((prev) => ({ ...prev, stock: { ...prev.stock, [name]: type === 'checkbox' ? checked : value } }));
  };

  const handleImage = async (e) => {
    const file = e.target.files?.[0];
    if (!file) return;
    try {
      setLoading(true);
      const url = await uploadItemImage(file, id || form.code || form.name);
      setForm((prev) => ({ ...prev, imageUrl: url }));
      showSuccess('Image uploaded');
    } catch (err) {
      showError('Image upload failed');
    } finally { setLoading(false); }
  };

  const onSubmit = async (e) => {
    e.preventDefault();
    const v = validateItem(form);
    setErrors(v);
    if (Object.keys(v).length) { showError('Please fix the highlighted fields'); return; }
    try {
      setLoading(true);
      if (isEdit) {
        await updateItem(id, form);
        showSuccess('Item updated');
      } else {
        await createItem(form);
        showSuccess('Item created');
      }
      navigate('/admin/items');
    } catch (err) {
      showError('Save failed');
    } finally { setLoading(false); }
  };

  return (
    <div className="space-y-6">
      <div className="flex items-center justify-between">
        <Link to="/admin/items" className="inline-flex items-center px-3 py-2 border rounded"><FiArrowLeft className="mr-2"/>Back</Link>
        <button onClick={onSubmit} disabled={loading} className="px-4 py-2 bg-indigo-600 text-white rounded inline-flex items-center"><FiSave className="mr-2"/>{loading ? 'Saving...' : 'Save'}</button>
      </div>

      <form onSubmit={onSubmit} className="grid grid-cols-1 lg:grid-cols-3 gap-6">
        <div className="bg-white border rounded-lg p-6 lg:col-span-2 space-y-4">
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div>
              <label className="text-sm font-medium">Item Name *</label>
              <input name="name" value={form.name} onChange={handleChange} className={`mt-1 w-full border rounded px-3 py-2 ${errors.name ? 'border-red-500' : ''}`}/>
            </div>
            <div>
              <label className="text-sm font-medium">Type</label>
              <select name="type" value={form.type} onChange={handleChange} className="mt-1 w-full border rounded px-3 py-2">
                <option value="product">Product</option>
                <option value="service">Service</option>
              </select>
            </div>
            <div>
              <label className="text-sm font-medium">HSN</label>
              <input name="hsn" value={form.hsn} onChange={handleChange} className="mt-1 w-full border rounded px-3 py-2"/>
            </div>
            <div>
              <label className="text-sm font-medium">Item Code</label>
              <input name="code" value={form.code} onChange={handleChange} className="mt-1 w-full border rounded px-3 py-2"/>
            </div>
            <div>
              <label className="text-sm font-medium">Unit *</label>
              <input name="unit" value={form.unit} onChange={handleChange} className={`mt-1 w-full border rounded px-3 py-2 ${errors.unit ? 'border-red-500' : ''}`}/>
            </div>
            <div>
              <label className="text-sm font-medium">Category</label>
              <input name="category" value={form.category} onChange={handleChange} className="mt-1 w-full border rounded px-3 py-2"/>
            </div>
          </div>

          <div>
            <label className="text-sm font-medium">Notes</label>
            <textarea name="notes" value={form.notes} onChange={handleChange} rows={3} className="mt-1 w-full border rounded px-3 py-2"/>
          </div>
        </div>

        <div className="bg-white border rounded-lg p-6 space-y-4">
          <div>
            <label className="text-sm font-medium">Item Image</label>
            <div className="mt-1 flex items-center space-x-3">
              {form.imageUrl && (<img src={form.imageUrl} alt="preview" className="h-16 w-16 object-cover rounded" />)}
              <label className="inline-flex items-center px-3 py-2 border rounded cursor-pointer"><FiUpload className="mr-2"/> Upload<input type="file" className="hidden" accept="image/*" onChange={handleImage}/></label>
            </div>
          </div>
          <div>
            <label className="text-sm font-medium">Status</label>
            <select name="status" value={form.status} onChange={handleChange} className="mt-1 w-full border rounded px-3 py-2">
              <option value="active">Active</option>
              <option value="inactive">Inactive</option>
            </select>
          </div>
        </div>

        <div className="bg-white border rounded-lg p-6 lg:col-span-3">
          <h3 className="font-semibold mb-3">Pricing</h3>
          <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
            <div>
              <label className="text-sm font-medium">Sale Price</label>
              <input name="salePrice" type="number" step="0.01" value={form.pricing.salePrice} onChange={handlePricingChange} className={`mt-1 w-full border rounded px-3 py-2 ${errors.salePrice ? 'border-red-500' : ''}`}/>
            </div>
            <div>
              <label className="text-sm font-medium">Purchase Price</label>
              <input name="purchasePrice" type="number" step="0.01" value={form.pricing.purchasePrice} onChange={handlePricingChange} className={`mt-1 w-full border rounded px-3 py-2 ${errors.purchasePrice ? 'border-red-500' : ''}`}/>
            </div>
            <div>
              <label className="text-sm font-medium">Tax Rate (%)</label>
              <input name="taxRate" type="number" step="0.01" value={form.pricing.taxRate} onChange={handlePricingChange} className={`mt-1 w-full border rounded px-3 py-2 ${errors.taxRate ? 'border-red-500' : ''}`}/>
            </div>
            <div>
              <label className="text-sm font-medium">Tax Mode</label>
              <select name="taxMode" value={form.pricing.taxMode} onChange={handlePricingChange} className="mt-1 w-full border rounded px-3 py-2">
                <option value="without_tax">Without Tax</option>
                <option value="with_tax">With Tax</option>
              </select>
            </div>
            <div>
              <label className="text-sm font-medium">Discount Type</label>
              <select name="discountType" value={form.pricing.discountType} onChange={handlePricingChange} className="mt-1 w-full border rounded px-3 py-2">
                <option value="percentage">Percentage</option>
                <option value="flat">Flat</option>
              </select>
            </div>
            <div>
              <label className="text-sm font-medium">Discount Value</label>
              <input name="discountValue" type="number" step="0.01" value={form.pricing.discountValue} onChange={handlePricingChange} className="mt-1 w-full border rounded px-3 py-2"/>
            </div>
          </div>
        </div>

        <div className="bg-white border rounded-lg p-6 lg:col-span-3">
          <h3 className="font-semibold mb-3">Stock</h3>
          <div className="grid grid-cols-1 md:grid-cols-4 gap-4 items-end">
            <div className="md:col-span-1 flex items-center space-x-2">
              <input type="checkbox" name="track" checked={form.stock.track} onChange={handleStockChange} />
              <label className="text-sm font-medium">Track Stock</label>
            </div>
            <div>
              <label className="text-sm font-medium">Opening Qty</label>
              <input name="openingQty" type="number" step="0.01" value={form.stock.openingQty} onChange={handleStockChange} className="mt-1 w-full border rounded px-3 py-2"/>
            </div>
            <div>
              <label className="text-sm font-medium">Minimum Qty</label>
              <input name="minQty" type="number" step="0.01" value={form.stock.minQty} onChange={handleStockChange} className="mt-1 w-full border rounded px-3 py-2"/>
            </div>
            <div>
              <label className="text-sm font-medium">Location</label>
              <input name="location" value={form.stock.location} onChange={handleStockChange} className="mt-1 w-full border rounded px-3 py-2"/>
            </div>
          </div>
        </div>
      </form>
    </div>
  );
}


